<template>
	<div>
		<el-table
				:data="tableData"
				style="width: 100%">
			<el-table-column
					prop="title"
					label="标题"
					width="180">
			</el-table-column>
			<el-table-column
					prop="username"
					label="发起人"
					width="180">
			</el-table-column>
			<el-table-column
					prop="portrait"
					label="头像">
			</el-table-column>
			<el-table-column
					prop="create_at"
					label="创建时间">
			</el-table-column>

			<el-table-column
					label="操作">
				<template slot-scope="scope">
					<el-button @click="dynamic(scope.row.id)"  type="info" size="small">新增动态</el-button>
					<el-button @click="prove(scope.row.id)"  type="primary" size="small">证明</el-button>
					<el-button @click="order(scope.row.id)" type="success"  size="small">捐款</el-button>
				</template>
			</el-table-column>
		</el-table>



		<el-dialog title="证明" :visible.sync="dialogProveVisible">

			<el-form :model="proveForm">
				<el-form-item label="与X关系" label-width="80px">
					<el-input v-model="proveForm.relation" placeholder="同事/朋友/亲戚" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="介绍" label-width="80px">

					<el-input v-model="proveForm.introduce" placeholder="说的是真的一样"  autocomplete="off"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="dialogProveVisible = false">取 消</el-button>
				<el-button type="primary" @click="doProve()">确 定</el-button>
			</div>
		</el-dialog>

		<el-dialog title="捐款" :visible.sync="dialogOrderVisible">
			<el-form :model="orderForm">
				<el-form-item label="金额" label-width="80px">
					<el-input v-model="orderForm.amount" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="鼓励语" label-width="80px">
					<el-input v-model="orderForm.encourage" autocomplete="off"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="dialogOrderVisible = false">取 消</el-button>
				<el-button type="primary" @click="doOrder()">确 定</el-button>
			</div>
		</el-dialog>


		<el-dialog title="动态" :visible.sync="dialogDynamicVisible">
			<el-form :model="dynamicForm">
				<el-form-item label="动态描述" label-width="80px">
					<el-input v-model="dynamicForm.title" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="上传图片" label-width="80px">
					<el-upload
							action="/api/auth/upload"
							list-type="picture-card"
							:on-success="handleImageSuccess"
					>
						<i class="el-icon-plus"></i>
					</el-upload>
					<el-dialog :visible.sync="dialogVisible">
						<img width="100%" :src="dialogImageUrl" alt="">
					</el-dialog>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="dialogDynamicVisible = false">取 消</el-button>
				<el-button type="primary" @click="doDynamic()">确 定</el-button>
			</div>
		</el-dialog>
	</div>
</template>


<script>
  export default {
    data() {
      return {
        tableData: [],
        dialogOrderVisible: false,
        dialogProveVisible: false,
        dialogDynamicVisible: false,
        orderForm: {
          id: 0,
          amount: 0,
          encourage: '',
				},
        proveForm: {
          id: 0,
          relation: '',
          introduce: '',
        },
        dynamicForm: {
          id: 0,
					image_list: [],
					title: '',
        },
      }
    },
    methods: {
      loadList() {
        this.$httpGet("admin/projectList", this.form).then((response) => {
          if (response.code === 0) {
            this.tableData = response.data.list
          }
        }).catch(() => {
          this.$message({
            message: '请求服务器异常, 请稍后再试',
            center: true,
            type: "error"
          });
        })
      },
			order(id) {
        this.orderForm.id = id
        this.dialogOrderVisible = true
      },
      prove(id) {
        this.proveForm.id = id
        this.dialogProveVisible = true
      },
      dynamic(id) {
        this.dynamicForm.id = id
        this.dialogDynamicVisible = true
      },
      doOrder() {
        this.dialogOrderVisible = false
      },
      doProve() {
        this.dialogProveVisible = false
      },
      doDynamic() {
        console.log()
        this.dialogDynamicVisible = false
      },
			// eslint-disable-next-line no-unused-vars
      handleImageSuccess(res, file){
        if(res.code === 0) {
          this.dynamicForm.image_list.push(res.data.file)
        }
      },
    },
    created() {
      this.loadList()
    }
  }
</script>

